___CSS_LOADER_EXPORT___.push([module.id, `:root {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;

  --gameboard-col: #e1e1e1;
  --header-footer-col: #1d1d1d;
  --game-gard-mildwhite: rgb(250, 250, 250);
  --correct-green: #37eb31;
  --incorrect-red: #fe0000;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

header {
  font-size: 1.5rem;
  padding: 0.4rem;
  font-weight: bold;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 1rem;
  gap: 1.5rem;
}

footer {
  font-size: 1rem;
  padding: 0.4rem;
  font-weight: bold;
}

.game-board {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 80vw;
  min-height: 50vh;
}

.game-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-color: var(--game-gard-mildwhite);
  padding: 1.5rem;
}

.game-card .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-card,
button {
  font-size: 1.5rem;
  font-weight: bold;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  gap: 3rem;
}

button {
  padding: 0.5em 2em;
}

.game-card .img-container img {
  width: 150px;
}

.game-card .dir-container p {
  text-align: center;
}

button,
.game-card,
.game-board {
  border: 2px solid var(--header-footer-col);
  border-radius: 20px;
  box-shadow: var(--header-footer-col) 4px 4px 0 0;
  color: var(--header-footer-col);
}

button,
.game-board {
  background-color: var(--gameboard-col);
}

header,
footer {
  background-color: var(--header-footer-col);
  color: var(--gameboard-col);
}

.correct {
  background-color: var(--correct-green);
}

.incorrect {
  background-color: var(--incorrect-red);
}

/* more button stuff */

button {
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

button:hover {
  background-color: var(--game-gard-mildwhite);
}

button:active {
  box-shadow: var(--header-footer-col) 2px 2px 0 0;
  transform: translate(2px, 2px);
}

.disabled,
button:hover.disabled {
  background-color: darkgrey;
}
